<template>
	<div>
		<h3>Choose type game</h3>
		<div class="wrapper_label">
			<label
				v-for="(item, ind) of arrayLabel"
				v-bind:key="ind"
			>
				<input type="radio" name="type"
					v-bind:checked="item === 'Normal'"
					v-bind:value="item"
					v-bind:disabled="!changeOptions"
					@click="$emit('set-type', $event.target.value)"
				/>{{item}}
			</label>
		</div>
	</div>
</template>

<script>
export default {
	props: {
		changeOptions: Boolean
	},
	data() {
		return {
			arrayLabel: ['Normal', 'Sound-only', 'Light-only', 'Crazy-board'],
		}
	}
}
</script>

<style lang="sass" scoped>
div
	display: flex
	flex-direction: column
	.wrapper_label
		flex-direction: row
	label
		margin-right: 5px
</style>
