<template>
		<ul class="simon">
			<li
				v-for="(item, ind) of classBtn"
				:key="ind"
				v-bind:class="[classActive[active[ind]] ? 'active' : '', item]"
				v-bind:id="ind+1"
				@click="$emit('player-button',  $event.target.id)"
			></li>
		</ul>
</template>

<script>
export default {
	props: {
		classActive: {
			redBtn: Boolean,
			blueBtn: Boolean,
			yellowBtn: Boolean,
			greenBtn: Boolean,
		}
	},
	data() {
		return {
			classBtn: ['red', 'blue', 'yellow', 'green'],
			active: ['redBtn', 'blueBtn', 'yellowBtn', 'greenBtn']
		}
	},
}
</script>

<style lang="sass" scoped>
.simon
	background: #fff
	position: relative
	float: left
	display: block
	margin-right: 3em
	width: 296px
	height: 290px
	-webkit-border-radius: 150px 150px 150px 150px
	border-radius: 150px 150px 150px 150px
	-moz-box-shadow: 2px 1px 12px #aaa
	-webkit-box-shadow: 2px 1px 12px #aaa
	-o-box-shadow: 2px 1px 12px #aaa
	box-shadow: 2px 1px 12px #aaa
.red, .blue, .yellow, .green
	opacity: 0.6
	height: 290px
	-webkit-border-radius: 150px 150px 150px 150px
	border-radius: 150px 150px 150px 150px
	position: absolute
	text-indent: 10000px
.red:hover, .blue:hover, .yellow:hover, .green:hover
	border: 2px solid black
.red
	background: #FF5643
	clip: rect(0px, 300px, 150px, 150px)
	width: 296px
.blue
	background: dodgerblue
	clip: rect(0px, 150px, 150px, 0px)
	width: 300px
.yellow
	background: #FEEF33
	clip: rect(150px, 150px, 300px, 0px)
	width: 300px
.green
	background: #BEDE15
	clip: rect(150px,300px, 300px, 150px)
	width: 296px
</style>
