<template>
	<div id="app">
		<h1>Simon Says</h1>
		<SimonPlay
			v-bind:classActive="classActive"
			v-on:player-button="pressingPlayer"
		/>
		<GameInfo
			v-on:start-game="startGame"
			v-bind:loser="loser"
			v-bind:round="round"
		/>
		<audio ref="music1" src="./assets/sounds/1.mp3"></audio>
		<audio ref="music2" src="./assets/sounds/2.mp3"></audio>
		<audio ref="music3" src="./assets/sounds/3.mp3"></audio>
		<audio ref="music4" src="./assets/sounds/4.mp3"></audio>
		<GameOptions
			v-bind:changeOptions="changeOptions"
			v-on:speed-option="speedOption"
			v-on:type-option="typeOption"
		/>
	</div>
</template>

<script>
import SimonPlay from '@/components/SimonPlay';
import GameInfo from '@/components/GameInfo';
import GameOptions from '@/components/GameOptions';

export default {
	name: 'App',
	data() {
		return {
			isStarted: false, //start indicator
			round: 0,	// round pointer
			combinationComp: [],
			combinationPlaeyr: [],
			checkedInput: false, //input permission
			changeOptions: true,
			loser: false,
			delaySound: 1500, //same game speed
			typeGame: 'Normal',	
			classObj: ['redBtn', 'blueBtn', 'yellowBtn', 'greenBtn'],
			classActive: {
				redBtn: false,
				blueBtn: false,
				yellowBtn: false,
				greenBtn: false,
			},
		}
	},
	components: {
		SimonPlay,
		GameInfo,
		GameOptions,
	},
	methods: {
		resetOnStart() {
			this.isStarted = false;
			this.round = 0;
			this.combinationComp = [];
			this.combinationPlaeyr = [];
			this.classActive.redBtn = false;
			this.classActive.blueBtn = false;
			this.classActive.yellowBtn = false;
			this.classActive.greenBtn = false;
			this.checkedInput = false;
			this.loser = false;
		},
		startGame() {
			this.changeOptions = false;
			this.resetOnStart();
			this.isStarted = true;
			this.addSound();
			this.playComp();
		},
		playComp() {
			this.checkedInput = false;
			const context = this;
			let delay = this.delaySound;
			this.combinationComp.forEach(function(sound, i, array) {
				if (i == array.length - 1) {
					setTimeout(function() {
						if (context.isStarted) {
							context.playSound(sound);
							context.checkedInput = true;
						}
					}, delay);
				}
				else {
					setTimeout(function() {
						context.playSound(sound);
					}, delay);
					delay += context.delaySound;
				}

			})
		},
		pressingPlayer(btn) {
			//if pressing is prohibited
			if (!this.checkedInput) return;
				this.playSound(btn);
				this.combinationPlaeyr.push(btn);
				// if the player's input is not correct
			if (this.combinationPlaeyr[this.combinationPlaeyr.length - 1] != this.combinationComp[this.combinationPlaeyr.length - 1]) {
				this.combinationPlaeyr = [];
				this.checkedInput = false;
				this.changeOptions = true;
				this.loser = true;
				return;
			}
			//if the last item in the check
			if (this.combinationPlaeyr.length == this.combinationComp.length) {
				const context = this;
				this.combinationPlaeyr = [];
				setTimeout(() => {
					context.addSound();
					context.playComp();
				}, context.delaySound)
			}
		},
		addSound() {
			this.round++;
			this.combinationComp.push(this.randomNumber());
		},
		clearBtn() {
			this.classActive.redBtn = false;
			this.classActive.blueBtn = false;
			this.classActive.yellowBtn = false;
			this.classActive.greenBtn = false;
		},
		playSound(btn) {
			const delay = this.delaySound - 150;
			const context = this;
			const localObj = {
				1() {
					context.$refs.music1.pause();
					context.$refs.music1.currentTime = 0;
					context.$refs.music1.play();
				},
				2() {
					context.$refs.music2.pause();
					context.$refs.music2.currentTime = 0;
					context.$refs.music2.play();
				},
				3() {
					context.$refs.music3.pause();
					context.$refs.music3.currentTime = 0;
					context.$refs.music3.play();
				},
				4() {
					context.$refs.music4.pause();
					context.$refs.music4.currentTime = 0;
					context.$refs.music4.play();
				}
			}
			if (this.typeGame === 'Normal') {
				localObj[btn]()
				this.classActive[this.classObj[btn-1]] = true;
			} else if(this.typeGame === 'Sound-only') {
				localObj[btn]()
			} else if (this.typeGame === 'Light-only') {
				this.classActive[this.classObj[btn-1]] = true;
			} else if (this.typeGame === 'Crazy-board') {
				const randome = Math.round(Math.random());
				if (randome) {
					localObj[btn]()
				} else {
				this.classActive[this.classObj[btn-1]] = true;
				}
			}
			setTimeout(this.clearBtn, delay);
		},
		stopTones() {
			this.$refs.music1.pause();
			this.$refs.music2.pause();
			this.$refs.music3.pause();
			this.$refs.music4.pause();
			this.$refs.music1.currentTime = 0;
			this.$refs.music2.currentTime = 0;
			this.$refs.music3.currentTime = 0;
			this.$refs.music4.currentTime = 0;
		},
		randomNumber() {
			return Math.floor(1 + Math.random() * 4);
		},
		speedOption(speed) {
			this.delaySound = +speed;
		},
		typeOption(type) {
			this.typeGame = type;
		},
	}
}
</script>

