<template>
	<div>
		<h3>Choose speed game</h3>
		<div class="wrapper_label">
			<label
				v-for="(item, ind) of arraySpeed"
				v-bind:key="ind"
			>
				<input type="radio" name="speed"
					v-bind:checked="item === 1500"
					v-bind:value="item"
					v-bind:disabled="!changeOptions"
					@click="$emit('set-speed', $event.target.value)"
				>
			{{arrayLabel[ind]}}
			</label>
		</div>
	</div>
</template>
<script>
	export default {
		props: {
			changeOptions: Boolean
		},
		data() {
			return {
				arraySpeed: [1500, 1000, 500],
				arrayLabel: ['Slowly', 'Normal', 'Fast']
			}
		}
	}
</script>

<style lang="sass" scoped>
div
	display: flex
	flex-direction: column
	.wrapper_label
		flex-direction: row
	label
		margin-right: 15px
</style>
