<template>
	<div class="game-options">
		<h2>Game Options:</h2>
		<TypeGame
			v-bind:changeOptions="changeOptions"
			v-on:set-type="setType"
		/>
		<TypeSpeed
			v-bind:changeOptions="changeOptions"
			v-on:set-speed="setSpeed"
		/>
	</div>
</template>

<script>
import TypeGame from '@/components/TypeGame';
import TypeSpeed from '@/components/TypeSpeed';
export default {
	props: {
		changeOptions: Boolean,
	},
	components: {
		TypeGame,
		TypeSpeed
	},
	methods: {
		setSpeed(speed) {
			this.$emit('speed-option', speed);
		},
		setType(speed) {
			this.$emit('type-option', speed);
		},

	}
}
</script>

<style lang="sass" scoped>
.game-options h2
	margin-top: 30px
	margin-bottom: 0

.game-options input[type="radio"]
	margin-right: 10px
	background-color: red

.hoverable:hover
	cursor: pointer
</style>
